    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    bottom: 0;
}

.toggleBtn.open span:after{ display: none;}

.toggleBtn span:before{
    content: ''; display: block;
    width: 100%; height: 4px; background-color: #000000;
    position: absolute; left: 0; bottom: calc(100% + 4px);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.toggleBtn span:after{
    content: ''; display: block;
    width: 100%; height: 4px; background-color: #000000;
    position: absolute; left: 0; top: calc(100% + 4px);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.mask{
    position: absolute; top: 0; right: 0;
    width: 100%;
    height: 100%;
    min-height: calc(500px + 1rem);
    background-color: rgba(0,0,0,0.6);
    z-index: -5;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    opacity: 0;
}

/* navopen
----------------------------------------------------------*/

.mainOpen #main, .mainOpen footer{
    left: -4.5rem;
}

.mainOpen .mask{
    z-index: 99;
    right: 4.5rem;
    opacity: 1;
}

/* footer
------------------------------------------------------------------------------*/

footer{
    position: absolute;
    z-index: 3;
    text-align: right;
    bottom: 0;
    left: 0;
    width: 100%;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.copyright{
    font-size: 0.36rem;
    text-shadow: 0 0 3px rgba(255,255,255,0.9), 0 0 1px rgba(255,255,255,0.9), 0 0 1px rgba(255,255,255,0.9);
}

/* mainLogo */

.mainLogo{
    position: fixed;
    top: 0.3rem;
    left: 0.3rem;
    z-index: 9990;
    width: 4rem;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.index .mainLogo{ width: 5.5rem;}

.mainLogo img.showUp{
    opacity: 0.8;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.mainLogo a:hover img.showUp{
    opacity: 0;
}

.mainLogo img.hideUp{
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.mainLogo a:hover img.hideUp{
    opacity: 1;
}

.mainLogoBG{
    content: '';
    display: block;
    width: 100%; height: 8rem;
    background-color: rgba(237,187,101,0.1);
    position: fixed;
    top: -5rem;
    right: 19%;
    z-index: -1;
    -webkit-transform: rotate(-17deg);
            transform: rotate(-17deg);
    -webkit-box-shadow: 3px 3px 3px rgba(0,0,0,0.1);
            box-shadow: 3px 3px 3px rgba(0,0,0,0.1);
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.mainLogoBG:hover{
    background-color: rgba(237,187,101,0.3);
}

.pageTlt{
    font-family: 'Satisfy', 'Josefin Slab', '\5FAE\8EDF\6B63\9ED1\9AD4', Microsoft JhengHei, Arial;
    width: 3.6em;
    font-weight: 300;
    color: rgba(0,0,0,0.8);
    font-size: 2.2rem;
    text-align: left;
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
    position: absolute;
    left: 2rem;
    top: 1.5rem;
    text-shadow: 0 0 1px rgba(255,255,255,0.5);
    cursor: default;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.mens_grooming .pageTlt{
    font-size: 1.5rem;
    width: 6em;
}

.conceptual_beauty .pageTlt{
    font-size: 1.5rem;
    width: 7em;
}

/* mainOpen mainLogo */

.mainOpen .mainLogo,
.mainOpen .pageTlt{
    -webkit-transform: translateX(-4.5rem);
            transform: translateX(-4.5rem);
}

.mainOpen .mainLogoBG{
    right: calc(19% + 4.5rem);
}

/* loadingBar
--------------------------------------------------------*/

.loadingBar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9995;
    background-color: rgba(0,0,0,0.9);
}

.loadingBar > div{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
    font-size: 0.4rem;
    color: #ffffff;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}@charset "utf-8";


